.time-converter {
  display: flex;
  flex-direction: column; /* Arrange elements from top to bottom */
  align-items: center; /* Center align elements horizontally */
  width: 200px; /* Set a fixed width for the container */
  margin-left: 1050px ;
  margin-top: -150px ; 
  padding: 20px; /* Add some padding around the container */
  border: 1px solid #ccc; /* Optional: Add a border around the container */
  border-radius: 10px; /* Optional: Add rounded corners to the container */
  background-color: #f9f9f9; /* Optional: Set a background color for the container */
}

.time-converter input,
.time-converter select,
.time-converter button {
  width: 100%; /* Make input, select, and button take full width of the container */
  margin-bottom: 10px; /* Add some space between elements */
  padding: 10px; /* Add padding inside the elements */
  font-size: 1rem; /* Set font size */
  border: 1px solid #ccc; /* Add border to input, select, and button */
  border-radius: 5px; /* Add rounded corners to input, select, and button */
}

.time-converter button {
  background-color: #1db954; /* Set background color for the button */
  color: #fff; /* Set text color for the button */
  cursor: pointer; /* Change cursor to pointer on hover */
}

.time-converter button:hover {
  background-color: #1aa34a; /* Change button color on hover */
}

.time-converter h2 {
  margin-top: 20px; /* Add space above the result */
  color: #000; /* Set text color for the result */
}
