/* App.css */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

.app {
  font-family: 'Roboto', sans-serif;
  position: relative;
  color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.content {
  flex-grow: 1;
  padding: 20px;
  background: rgba(0, 0, 0, 0);
  color: #ffffff;
  min-height: 100vh;
  overflow: hidden;
  position: relative;
}

.content div {
  transform: translateZ(0);
  transition: transform 0.5s ease-in-out;
}
